* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}


body {
  color: #222222;
  font-family: Arial, sans-serif;
  font-size: 18px;
  line-height: 1.5;
  background-color: #EFEFEF;
}
.container {
  width:75%;
  margin: 140px auto 0;

}

.no_pad_left {
  padding-left: 0 !important;
  // background-color: red;
}

.no_pad_right {
  padding-right: 0 !important;
  // background-color: blue;
}



.who_section {
  // margin: 140px auto 0;
  // // width: 75%;

  h1 {
    color: #222222;
    font-size: 90px;
    line-height: 80px;
    margin: 0;
  }

  .who_history {
    margin-top: 0;
    // float:left;
  }

  .half_column {
    float: left;
    position: relative;
    width: 50%;
    padding: 30px;
    // background-color: red;
    // height: 40vh;
  }
  img {
    width: 75%
  }
}


hr {
  margin: 60px 0;
}

.header-0 {
  font-size: 90px;
  line-height: 80px;
}

.pre-header {
  font-size: 24px;
  margin-bottom: 40px;
}

.exec {
    margin-bottom: 40px;
}

.column-quarter {
  float: left;
  position: relative;
  width: 23.5%;
  margin-top: 1%;
  margin-right: .5%;
  margin-bottom: 1%;
  margin-left:.5%;
}

.exec-pic {
  width: 100%;
  display: block;

  img {
    width:100%;
    margin-right: 1%;
  }

  h3 {
    font-size: 24px;
    line-height: 1;
    margin-bottom: 0;
  }

}
.exec-name {
  font-size: 18px;
  width:100%;
  // background-color: red;
}
